@import "https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Titan+One&display=swap";
:root {
  --primary-color: #fff;
  --first-part: #ffb8ca;
  --second-part: #f0d1a5;
  --third-part: #c2e297;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html, *, :before, :after {
  box-sizing: border-box;
}

body {
  background: var(--primary-color);
  color: var(--primary-color);
  letter-spacing: .03em;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
}

h1, h2, h3, ul, p {
  margin: 0;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.link {
  color: currentColor;
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

a, button {
  cursor: pointer;
  color: inherit;
  font-style: normal;
  text-decoration: none;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.customers__slider, .flex-column {
  min-width: 0;
}

.slick-list {
  overflow: hidden;
}

.slick-track {
  display: flex;
}

.customers__slider {
  position: relative;
}

.customers__slider .slick-arrow {
  z-index: 5;
  position: absolute;
  top: 50%;
}

.customers__slider .slick-arrow.slick-prev {
  right: 0;
}

.customers__slider .slick-arrow.slick-next {
  left: 0;
}

.customers__slider .slick-dots {
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  display: flex;
}

.customers__slider .slick-dots li {
  margin: 0 9px;
  list-style: none;
}

.customers__slider .slick-dots button {
  width: 12px;
  height: 12px;
  background: #d9d9d9;
  border: 0;
  border-radius: 50%;
  font-size: 0;
}

.customers__slider .slick-dots li.slick-active button {
  background: url("Vector.8694a085.svg") center / 100% no-repeat;
  border-radius: 0%;
}

