/* reset */
h1,
h2,
h3,
ul,
p {
  padding-bottom: 0px;
  padding-top: 0px;
  margin: 0px;
  padding-left: 0px;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

.link {
  text-decoration: none;
  color: currentColor;
}

.list {
  
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a,
button {
  text-decoration: none;
  cursor: pointer;
  font-style: normal;
  color: inherit;
}